import React from "react";
import { NavbarContainer, NavLinks } from "./NavbarElements";

const index = () => {
  return (
    <>
      <NavbarContainer>
        <NavLinks to="/">Home</NavLinks>
        <NavLinks to="/tentang">Tentang Kami</NavLinks>
        <NavLinks to="/training">Training + Sertifikat</NavLinks>
        <NavLinks to="/produk">Produk</NavLinks>
        <NavLinks to="/kontak">Kontak Kami</NavLinks>
        <NavLinks to="/jobs">Lowongan Kerja</NavLinks>
      </NavbarContainer>
    </>
  );
};

export default index;
