import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./pages/index";
import Tentang from "./pages/tentang";
import Training from "./pages/training.js";
import Produk from "./pages/produk";
import Kontak from "./pages/kontak";
import Jobs from "./pages/jobs";

function App() {
  return (
    <>
      <Router>
        <Switch>
          <Route path="/" component={Home} exact />
          <Route path="/tentang" component={Tentang} exact />
          <Route path="/training" component={Training} exact />
          <Route path="/produk" component={Produk} exact />
          <Route path="/kontak" component={Kontak} exact />
          <Route path="/jobs" component={Jobs} exact />
        </Switch>
      </Router>
    </>
  );
}

export default App;
